import React from "react";
import { CustomColumns, Feature, Translation } from "../../types";
import { useQuery } from "react-query3";
import { Button, PageHeader, Space } from "antd";
import { Modal } from "components/ui/Modal";
import { Link } from "react-router-dom";
import useAppStore from "components/context/app/Hooks";
import NotLicensed from "components/pages/notLicensed";
import { EditOutlined } from "@ant-design/icons";
import DocsButton from "components/docsButton";

const Table = React.lazy(() => import("components/ui/Table"));
const DeleteButton = React.lazy(() => import("components/ui/DeleteButton"));
const TranslationModal = React.lazy(() => import("./translationModal"));

export default function Tags() {
    const { data: tags, isLoading } = useQuery<Translation[]>("/translation");

    const { licensed } = useAppStore();

    const columns: CustomColumns<Translation>[] = [
        {
            key: "languageId",
            title: "Language Id",
            dataIndex: "languageId",
            width: "fit-content"
        },
        {
            key: "name",
            title: "Name",
            dataIndex: "name",
            width: "fit-content"
        },
        {
            align: "left",
            key: "stringCount",
            title: "Strings",
            dataIndex: "stringCount",
        },
        {
            width: "fit-content",
            dataIndex: "actions",
            editable: false,
            render: (text, record) => (
                <Space>
                    <Link to={`${record.languageId}`}>
                        <Button icon={<EditOutlined />} type="text">Edit</Button>
                    </Link>
                    <DeleteButton resource={record} allowedWithOneWayGitSync={false} />
                </Space>
            ),
        },
    ];

    if (!licensed(Feature.Automation)) {
        return <NotLicensed title="Translations are only available for licensed instances." />
    }

    return (
        <PageHeader title={"Translations"} subTitle="String translations you can use within your scripts." extra={[
            <DocsButton url="platform/translation" />
        ]}>
            <Table
                title="Translations"
                actions={[
                    <Modal>
                        <TranslationModal />
                    </Modal>,
                ]}
                columns={columns}
                data={tags}
                loading={isLoading}
                showHeader={true}
            />
        </PageHeader>
    );
}
